import {Modal, ModalHeader, ModalBody, Button, Label} from 'reactstrap';
import{LocalForm, Control, Errors} from 'react-redux-form';


const required = (val) => val && val.length;
const maxLength = (len) => (val)=> !(val) || (val.length <= len);
const minLength =(len) => (val) => !(val) || (val.length >= len);

export default function CommentForm({toggleModal,isModalOpen,productId,addComment}){

    const handleSubmit = (values)=>{
        console.log(values);
        toggleModal();
        addComment(productId,values.rating, values.author, values.comment);
    }

    return(
        <Modal isOpen={isModalOpen} toggle={toggleModal}>
            <ModalHeader toggle={toggleModal}>Header</ModalHeader>
            <ModalBody>
                <LocalForm onSubmit={handleSubmit}>
                    <div className='form-group'>
                        <Label for='reating'>Rating</Label>
                        <Control.select className='form-control' defaultValue={1} model='.rating' id='rating' name='rating'>
                            <option>1</option>
                            <option>2</option>
                            <option>3</option>
                            <option>4</option>
                            <option>5</option>
                        </Control.select>
                    </div>
                    <div className='form-group'>
                        <Label for='author'>You Name</Label>
                        <Control.text className='form-control'  model='.author' id='author' name='author' placeholder='Your Name' validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}/>
                        <Errors className='text-danger' model='.author' show='touched' messages={{
                            required:'Required',
                            minLength: 'Must be greater than 2 characters',
                            maxLength: 'Must be 15 characters or less'
                        }}/>
                    </div>
                    <div className='form-group'>
                        <Label for='comment'>Comment</Label>
                        <Control.textarea model='.comment' id='comment' name='comment' className='form-control' rows='7'/>
                    </div>
                    <Button color='primary'  type='submit'>Submit</Button>
                </LocalForm>
            </ModalBody>
        </Modal>
    );
}