import {Component} from 'react';
import { Envelope, TelephoneFill, Skype } from 'react-bootstrap-icons';
import  Map  from './MapComponent';
import {BreadcrumbItem, Breadcrumb , Label, Col, Row,  Button, Modal, ModalBody, ModalFooter, ModalHeader } from 'reactstrap';
import {Link} from 'react-router-dom';
import {Control, Errors, Form} from 'react-redux-form';


function ModalAlert({isModalOpen, toggleModal,values}){
    
    return(
        <Modal isOpen={isModalOpen} toggle={toggleModal}>
            <ModalHeader toggle={toggleModal}>
                Form's Data
            </ModalHeader>
            <ModalBody>
                <dl className="row">
                    <dt className="col-6">First Name: </dt>
                    <dd className="col-6">{values.firstname}</dd>
                    <dt className="col-6">Last Name: </dt>
                    <dd className="col-6">{values.lastname}</dd>
                    <dt className="col-6">Email: </dt>
                    <dd className="col-6">{values.email}</dd>
                    <dt className="col-6">Tel. Number: </dt>
                    <dd className="col-6">{values.telnum}</dd>
                    <dt className="col-6">Agreed To Call You: </dt>
                    <dd className="col-6">{values.agree?'Yes':'No'}</dd>
                    <dt className="col-6">Contact Type: </dt>
                    <dd className="col-6">{values.contactType}</dd>
                    <dt className="col-6">Your Feedback: </dt>
                    <dd className="col-6">{values.message}</dd>
                </dl>
            </ModalBody>
            <ModalFooter>
                <Button color="primary" onClick={toggleModal}>Ok</Button>
            </ModalFooter>
        </Modal>
    );
}

const required = (val) => val && val.length;
const maxLength = (len) => (val)=> !(val) || (val.length <= len);
const minLength =(len) => (val) => !(val) || (val.length >= len);
const validEmail = (val) => /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(val);
const isNumber = (val) => !isNaN(Number(val));


export default class Contact extends Component{
    constructor(props) {
        super(props);

        this.state = {
            values:{},
            isModalOpen: false
        }
        this.handleSubmit = this.handleSubmit.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
    }
        
     toggleModal(){
        this.setState({
            isModalOpen: !(this.state.isModalOpen)
          });
    }

    handleSubmit(values){
        this.setState({
            isModalOpen: !this.state.isModalOpen,
            values: values
        });
        this.props.resetFeedbackForm();
    }
    
    
   
    render(){
        return (
            <div className="container">
                <div className="row">
                    <Breadcrumb className="bread-crumb">
                        <BreadcrumbItem><Link to="/home">Home</Link></BreadcrumbItem>
                        <BreadcrumbItem active>Contact Us</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h2>Contact Us</h2>
                        <hr />
                    </div>                
                </div>
                <div className="row mt-5">
                    <div className="col-12 col-sm-4 col-offset-1">
                        <h5>Our Address</h5>
                        <address>
                                641, Clear Weather Day Street<br />
                                Clear Weather Day, Office<br />
                                Spain, Barcelona <br />
                                <TelephoneFill/> : +34 1234 5678<br />
                                <Envelope/> : <a href="mailto:easymarket@market.net">
                                easymarket@market.net</a>
                        </address>
                        <div className="btn-group" role="group">
                            <a role='button' className='btn btn-primary' href="tel:+3412345678"><TelephoneFill/> Call</a>
                            <a role='button' className='btn btn-info' href='https://www.skype.com/nl/features/skype-web/'><Skype/> Skype</a>
                            <a role='button' className='btn btn-success' href="mailto:easymarket@market.net"><Envelope/> Email</a>
                        </div>
                    </div>
                    <div className='col-12 col-md-6  d-none d-md-block col-offset-1'>
                        <Map/>
                    </div>
                    
                </div>
                <div className="row">
                    <hr />
                </div>
                <div className="row row-content">
                   <div className="col-12 mt-5">
                      <h3>Send us your Feedback</h3>
                      <hr />
                   </div>
                    <div className="col-12 col-md-9">
                        <Form model='feedback' onSubmit={(values) => this.handleSubmit(values)}>
                            <Row className="form-group">
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={10}>
                                    <Control.text model=".firstname" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        className="form-control"
                                        validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                        />
                                        <Errors
                                            className='text-danger'
                                            model='.firstname'
                                            show='touched'
                                            messages={{
                                                required:'Required',
                                                minLength: 'Must be greater than 2 characters',
                                                maxLength: 'Must be less than 15 characters'
                                            }}
                                        />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="lastname" md={2}>Last Name</Label>
                                <Col md={10}>
                                    <Control.text model=".lastname" id="lastname" name="lastname"
                                        placeholder="Last Name"
                                        className="form-control"
                                        validators={{required, minLength:minLength(3), maxLength:maxLength(15)}}
                                         />
                                         <Errors
                                            className='text-danger'
                                            model='.lastname'
                                            show='touched'
                                            messages={{
                                                required:'Required',
                                                minLength: 'Must be greater than 2 characters',
                                                maxLength: 'Must be less than 15 characters'
                                            }}
                                        />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="telnum" md={2}>Contact Tel.</Label>
                                <Col md={10}>
                                    <Control.text model=".telnum" id="telnum" name="telnum"
                                        placeholder="Tel. Number"
                                        className="form-control"
                                        validators={{
                                            required, minLength: minLength(3), maxLength: maxLength(15), isNumber
                                        }}
                                         />
                                          <Errors
                                        className="text-danger"
                                        model=".telnum"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            minLength: 'Must be greater than 2 numbers',
                                            maxLength: 'Must be 15 numbers or less',
                                            isNumber: ' Must be a number'
                                        }}
                                     />
                                </Col>
                               
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="email" md={2}>Email</Label>
                                <Col md={10}>
                                    <Control.text model=".email" id="email" name="email"
                                        placeholder="Email"
                                        className="form-control"
                                        validators={{
                                            required, validEmail
                                        }} />
                                        <Errors
                                        className="text-danger"
                                        model=".email"
                                        show="touched"
                                        messages={{
                                            required: 'Required',
                                            validEmail: ' Invalid Email Address'
                                        }}
                                     />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size: 6, offset: 2}}>
                                    <div className="form-check">
                                        <Label check>
                                            <Control.checkbox model=".agree" name="agree"
                                                className="form-check-input"
                                                 /> {' '}
                                                <strong>May we contact you?</strong>
                                        </Label>
                                    </div>
                                </Col>
                                <Col md={{size: 3, offset: 1}}>
                                    <Control.select model=".contactType" name="contactType"
                                        className="form-control">
                                        <option>Tel.</option>
                                        <option>Email</option>
                                    </Control.select>
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Label htmlFor="message" md={2}>Your Feedback</Label>
                                <Col md={10}>
                                    <Control.textarea model=".message" id="message" name="message"
                                        rows="12"
                                        className="form-control" />
                                </Col>
                            </Row>
                            <Row className="form-group">
                                <Col md={{size:10, offset: 2}}>
                                    <Button type="submit" color="primary">
                                    Send Feedback
                                    </Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </div>
                <ModalAlert toggleModal={this.toggleModal} isModalOpen={this.state.isModalOpen} values={this.state.values}
                />
            </div>
        );
    }
}