import { useState } from 'react';
import {
   Card, CardImg, CardBody,
   CardTitle,
   Breadcrumb,
   BreadcrumbItem
 } from 'reactstrap';
 import {Link} from 'react-router-dom';
 import { Loading } from './LoadingComponent';

 function RenderMenuItem({product}){
   return(
      
         <Card>
            <Link to={`/menu/${product.id}`}>
               <CardImg top  height='200px' src={product.image} alt={product.name} />
               <CardBody>
                  <CardTitle  className='align-text-center' tag="h5" color='#000'>{product.name}</CardTitle>
               </CardBody>
            </Link>
            <p className='product-price-menu-item'>{product.price}</p>
         </Card>

   );
 }


function Menu({products}){
     
     const [selectedProduct, setSelectedProduct] = useState(null);
      
      const menu = products.products.map((product)=>{
        return(
            <div  className='col-12 col-md-4 col-lg-3 m-1' key={product.id}>
               <RenderMenuItem product={product} onProductSelected={setSelectedProduct}/>
            </div>
        );
     });
     if (products.isLoading) {
      return(
          <div className="container">
              <div className="row">            
                  <div className="col-12 mt-5">
                        <Loading />
                  </div>
              </div>
          </div>
      );
  }
  else if (products.errMess) {
      return(
          <div className="container">
              <div className="row"> 
                  <div className="col-12">
                      <h4>{products.errMess}</h4>
                  </div>
              </div>
          </div>
      );
  }else{
         return(
            <div className="container">
               <div className="row">
               <Breadcrumb className='bread-crumb'>
                  <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                  <BreadcrumbItem active>Menu</BreadcrumbItem>
               </Breadcrumb>
               </div>
               <div className="row" >
                    <div className="col-12">
                        <h2 >Menu</h2>
                        <hr className='mb-5' />
                    </div>
               </div>
               <div className="row products-menu">
                    {menu}
               </div>
            </div>      
      );
  }
    
}

export default Menu;