import Menu from './MenuComponent';
import Header from './HeaderComponent';
import Footer from './FooterComponent';
import Home from './HomeComponent';
import Contact from './ContactComponent';
import ProductDetails from './ProductDetailsComponent';
import About from './AboutComponent';

import {Switch, Route, Redirect, withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import { addComment, fetchProducts } from '../redux/ActionCreators';
import { Component} from 'react';
import {actions} from 'react-redux-form';
import { TransitionGroup, CSSTransition } from 'react-transition-group';


const mapDispatchToProps = dispatch => ({
  
    addComment: (productId, rating, author, comment) => dispatch(addComment(productId, rating, author, comment)),
    fetchProducts: () => { dispatch(fetchProducts())},
    resetFeedbackForm : () =>{dispatch(actions.reset('feedback'))}
  });


const mapStateToProps = state =>{
    return {
        products: state.products,
        productsComments: state.productsComments,
        leaders: state.leaders,
        promotions: state.promotions
    }
}

class Main extends Component  {
    constructor(props){
        super(props);
    }
    componentDidMount() {
        this.props.fetchProducts();
      }
      
    render(){
        return(
            <>
                <Header/>
                <TransitionGroup>
                    <CSSTransition key={this.props.location.key} classNames='page' timeout={300}>
                        <Switch location={this.props.location}>
                            <Route path='/' exact component={
                                ()=> <Home product={this.props.products.products.filter(product => product.featured)[0]}
                                productsLoading={this.props.products.isLoading}
                                productsErrMess={this.props.products.errMess}
                                promotion={this.props.promotions.filter(promotion => promotion.featured)[0]} 
                                leader={this.props.leaders.filter(leader => leader.featured)[0]}/>}/>
                            <Route path="/menu/:productId" component={
                                ({match})=> <ProductDetails product={this.props.products.products.filter(product => product.id === parseInt(match.params.productId,10))[0]}
                                isLoading={this.props.products.isLoading}
                                errMess={this.props.products.errMess}
                                productComments={this.props.productsComments.filter(comment => comment.productId === parseInt(match.params.productId,10))}
                                addComment={this.props.addComment}
                                />}/>
                            <Route path='/menu' component={()=><Menu products={this.props.products} productsComments={this.props.productsComments} />}/>
                            <Route path='/contactus' component={()=><Contact resetFeedbackForm={this.props.resetFeedbackForm}/>}/>
                            <Route path='/aboutus' component={()=><About leaders={this.props.leaders}/>}/>
                            <Redirect to='/'/> 
                        </Switch>          
                    
                    </CSSTransition>
                </TransitionGroup>
                <Footer/>
            </>
        );
    }
}

export default withRouter(connect(mapStateToProps,mapDispatchToProps)(Main));