import {
    Card, CardImg,
    CardTitle,CardText, Breadcrumb, BreadcrumbItem, Button
  } from 'reactstrap';
import {Link} from 'react-router-dom';
import {Pencil} from 'react-bootstrap-icons';
import CommentForm from './CommentFormComponent';
import {useState} from 'react';
import { Loading } from './LoadingComponent';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';

function RenderProduct({product}){
    return(
        <FadeTransform in transformProps={{exitTransform: 'scale(2) translateY(-50%)'}}>
            <div>
                <Card >
                    <CardImg  className='product-details-img' src={product.image} alt={product.name}/>
                    
                </Card>
                <div className='mt-5'>
                    <h5>{product.fullName}</h5>
                </div>
                <hr />
                <div>
                    <p>Price: {product.price}</p>
                </div>
                <div className='mt-5'>
                    <p>{product.description}</p>
                </div>
            </div>
        </FadeTransform>

            );
}

function RenderComments({commentsList}){
    if(commentsList.length > 0){
        const comments = commentsList.map(comment =>{
            return(
                    <Fade in>
                        <div  key={comment.id}>
                            <Card className='product-comment-card'>
                                <CardTitle tag="h5">{comment.author}</CardTitle>
                                <CardText>{comment.comment}</CardText>
                                <CardText>
                                    <small className="text-muted">{new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))}</small>
                                </CardText>
                            </Card>
                        </div>
                    </Fade>                  
            );
        });
        return(
            <>
                <h4>Comments</h4>
                <hr />
                <Stagger in>
                    {comments}
                </Stagger>
            </>
        );
    }else{
        return(<div></div>);
    }

}

const ProductDetails = ({product,productComments,addComment,errMess,isLoading})=>{
    const [isCommentFormModalOpen,setIsCommentFormModalOpen] = useState(false);
    const toggleCommentFormModal =()=>{
        setIsCommentFormModalOpen(!isCommentFormModalOpen);     
    }
    if (isLoading) {
        return(
            <div className="container">
                <div className="row">            
                    <div className="col-12 mt-5">
                        <Loading />
                    </div>
                </div>
            </div>
        );
    }
    else if (errMess) {
        return(
            <div className="container">
                <div className="row">            
                    <h4>{errMess}</h4>
                </div>
            </div>
        );
    }
    else if (product != null) {
         
        return(
            <div className="container">
                <div className="row">
                    <Breadcrumb className='bread-crumb'>
                        <BreadcrumbItem><Link to='/home'>Home</Link></BreadcrumbItem>
                        <BreadcrumbItem><Link to='/menu'>Menu</Link></BreadcrumbItem>
                        <BreadcrumbItem active>{product.fullName}</BreadcrumbItem>
                    </Breadcrumb>
                </div>
                <div className="row">
                    <div className="col-12 col-md-4 " >
                        <RenderProduct product={product}/>
                    </div>
                    <div className="col-12 col-md-6 " >
                        <RenderComments commentsList={productComments}/>
                        <Button className='btn-submit-comment ml-4' outline  color="primary" onClick={toggleCommentFormModal} ><Pencil size="20px"/> Comment</Button>
                    </div>
                </div>
                <CommentForm toggleModal={toggleCommentFormModal} isModalOpen={isCommentFormModalOpen} productId={product.id} addComment={addComment}/>
            </div>
            
        );
     }
}

export default ProductDetails;